

header {
    .imagen{
        position: relative;
        width: 100%;
        height: 150px; 
        background-image: url('../../../build/img/header.webp');
        background-size: cover;
        background-repeat: no-repeat;
        
    }

    .header{
        width: 90%;
        margin: auto;
        padding: 30px 0px;

        h1{
            background-color: aliceblue;
