@import url("https://fonts.googleapis.com/css2?family=Raleway:wght@100;300;400;500;600;700&display=swap");
body {
  font-family: "Raleway", sans-serif;
  box-sizing: border-box;
  margin: 0px;
  padding: 0px;
}

h1 {
  font-size: 50px;
}

h2 {
  font-size: 40px;
}

p {
  font-size: 20px;
}

.bold {
  font-weight: 700;
}

.contenedor {
  display: flex;
  width: 90%;
  gap: 20px;
  align-items: flex-start;
  justify-content: flex-start;
  margin: 75px auto;
}

.seleccionado {
  font-weight: 700;
}

header .imagen {
  position: relative;
  width: 100%;
  height: 150px;
  background-image: url("../../../build/img/header.webp");
  background-size: cover;
  background-repeat: no-repeat;
}
header .header {
  width: 90%;
  margin: auto;
  padding: 30px 0px;
}
header .header h1 {
  background-color: aliceblue;
  padding: 30px 50px 30px 0px;
  border-radius: 10px;
  width: 45%;
  text-align: center;
  font-weight: 500;
}

.week {
  width: 90%;
  margin: auto;
}
.week .bodyAjustes {
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.week .bodyAjustes > div {
  position: relative;
}
.week .bodyAjustes button {
  border-radius: 10px;
  border: none;
  padding: 0px 10px;
  font-size: 19px;
  margin-left: 5px;
  height: 50px;
}
.week .bodyAjustes button:hover {
  background-color: rgb(111, 124, 135);
  color: white;
  cursor: pointer;
}
.week .bodyAjustes button:active {
  background-color: white;
  color: black;
}
.week .bodyAjustes .ocultarGrupos {
  position: absolute;
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .ocultarGrupos p {
  font-size: 0px;
  margin: 0px;
  padding: 0px;
}
.week .bodyAjustes .ocultarGrupos img {
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .mostrarGrupos {
  padding: 30px 10px 10px 10px;
  position: absolute;
  display: flex;
  flex-direction: column;
  gap: 20px;
  left: 205px;
  top: 60px;
  background-color: #527581;
  width: 200px;
  height: auto;
  color: white;
  border-radius: 5px;
}
.week .bodyAjustes .mostrarGrupos p {
  font-size: 20px;
}
.week .bodyAjustes .mostrarGrupos .agregarGrupos {
  font-weight: 300;
  color: rgb(255, 255, 255);
  padding: 10px 5px;
  margin-top: -10px;
  border-radius: 5px;
}
.week .bodyAjustes .mostrarGrupos .agregarGrupos:hover {
  background-color: rgb(111, 124, 135);
  cursor: pointer;
}
.week .bodyAjustes .mostrarGrupos .imagenesGrupo {
  padding: 0px;
  margin: 0px;
  width: 0px;
  height: 0px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo {
  height: 20px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo p {
  padding: 0px;
  font-size: 18px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo img {
  width: 0px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo:hover .imagenesGrupo {
  position: relative;
  top: -27px;
  left: 124px;
  width: 71px;
  height: 20px;
  background-color: rgba(53, 68, 90, 0.95);
  display: flex;
  align-items: center;
  justify-content: center;
  border-radius: 5px;
  padding: 3px;
}
.week .bodyAjustes .mostrarGrupos .infoGrupo:hover img {
  padding: 3px;
  border-radius: 3px;
  width: 20px;
  filter: invert(100%);
}
.week .bodyAjustes .mostrarGrupos .infoGrupo:hover img:hover {
  background-color: rgba(255, 255, 255, 0.95);
}
.week .bodyAjustes .mostrarGrupos .linea {
  border-bottom: 1px solid;
}
.week .bodyAjustes .mostrarGrupos p {
  padding: 0px;
  margin: 3px;
}
.week .bodyAjustes > div {
  display: flex;
  gap: 20px;
  align-items: center;
}
.week .dias {
  overflow: auto;
}
.week .dias > div {
  display: flex;
  justify-content: center;
  align-items: stretch;
  gap: 15px;
  width: 2555px;
}
.week .dias > div .dia {
  width: 350px;
  min-height: 400px;
  padding: 30px 12px;
  border-radius: 3px;
  margin: 30px 0px;
}
.week .dias > div .dia .headerDia {
  display: flex;
  justify-content: space-between;
  padding-left: 5px;
}
.week .dias > div .dia .headerDia > div {
  display: flex;
  gap: 10px;
}
.week .dias > div .bodyDia p,
.week .dias > div .headerDia p {
  padding-top: 0px;
  padding-bottom: 10px;
  margin: 0px;
}
.week .dias > div .bodyDia .agregarTareas {
  font-weight: 300;
  color: rgb(255, 255, 255);
  padding: 10px 5px;
  border-radius: 5px;
  margin: 10px 5px;
}
.week .dias > div .bodyDia .agregarTareas:hover {
  background-color: rgb(111, 124, 135);
  cursor: pointer;
}
.week .dias > div .bodyDia .agregarTareas p {
  padding: 0px;
}
.week .dias > div .formularioTarea {
  position: relative;
  margin-bottom: 20px;
}
.week .dias > div .formularioTarea form,
.week .dias > div .formularioTarea .select,
.week .dias > div .formularioTarea .subject {
  background-color: rgb(225, 233, 240);
}
.week .dias > div .formularioTarea .select p {
  color: rgb(91, 91, 91);
  font-size: 17px;
  padding: 10px;
}
.week .dias > div .formularioTarea .select p:hover {
  background-color: rgb(146, 167, 185);
  color: white;
  cursor: pointer;
}
.week .dias > div .formularioTarea .subjectOculto {
  height: 0px;
  width: 0px;
}
.week .dias > div .formularioTarea .subjectOculto p {
  height: 0px;
  font-size: 0px;
  padding: 0px;
}
.week .dias > div .formularioTarea .subject {
  max-height: 250px;
  position: absolute;
  width: 100%;
  overflow-y: auto;
  z-index: 1;
}
.week .dias > div .formularioTarea .subject p {
  height: auto;
  font-size: 17px;
}
.week .dias > div .formularioTarea .subject p:hover {
  background-color: rgb(146, 167, 185);
  color: white;
  cursor: pointer;
}
.week .dias > div .formularioTarea .gurpos {
  max-height: 250px;
  width: 100%;
  overflow-y: auto;
}
.week .dias > div .formularioTarea .grupos p {
  font-size: 17px;
  padding-left: 15px;
}
.week .dias > div .formularioTarea .grupos p:hover {
  background-color: rgb(146, 167, 185);
  color: white;
  cursor: pointer;
}
.week .dias > div .formularioTarea .grupos p::before {
  content: ": : ";
}
.week .dias > div .formularioTarea .gruposOcultos {
  height: 0px;
  width: 0px;
}
.week .dias > div .formularioTarea .gruposOcultos p {
  font-size: 0px;
  padding: 0px;
}
.week .dias > div .formularioTarea form input {
  border: 0px;
  background-color: transparent;
  padding: 10px;
  font-size: 17px;
}
.week .dias > div .formularioTarea form input:focus {
  outline: none;
  border: 0px solid white;
}
.week .dias > div .mostrarTarea {
  display: flex;
  flex-direction: column;
}
.week .dias > div .mostrarTarea .headerTarea {
  position: relative;
  display: flex;
  align-items: center;
  justify-content: space-between;
  background-color: #527581;
  padding: 15px;
  border-radius: 5px;
  margin-bottom: 10px;
}
.week .dias > div .mostrarTarea .headerTarea .tarea {
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;
  width: 100%;
  gap: 10px;
  z-index: 0;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre {
  width: 100%;
  display: flex;
  align-items: flex-start;
  justify-content: space-between;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre .tareaAjustes {
  display: flex;
  position: absolute;
  left: 215px;
  top: 7px;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre .tareaAjustes img {
  width: 0px;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaNombre > p {
  font-size: 18px;
}
.week .dias > div .mostrarTarea .headerTarea .tarea .tareaGrupo > p {
  font-size: 16px;
  font-weight: 300;
}
.week .dias > div .mostrarTarea .headerTarea .tarea p {
  padding: 0px;
}
.week .dias > div .mostrarTarea .headerTarea:hover .tarea .tareaNombre .tareaAjustes img {
  width: 20px;
  filter: invert(100%);
  z-index: 1;
  padding: 5px 7px 5px 7px;
}
.week .dias > div .mostrarTarea .headerTarea:hover .tarea .tareaNombre .tareaAjustes {
  background-color: rgba(53, 68, 90, 0.95);
  border-radius: 5px;
}
.week .dias > div .mostrarTarea .headerTarea:hover .tarea .tareaNombre .tareaAjustes img:hover {
  background-color: rgba(255, 255, 255, 0.95);
  border-radius: 5px;
  cursor: pointer;
}
.week .tabla {
  overflow: hidden;
}
.week .tabla.dias > div {
  flex-wrap: wrap;
  width: auto;
}
.week .tabla.dias > div .dia {
  flex: 1 1 350px;
  max-width: 350px;
}

.lunes {
  color: white;
  background-color: #3A98B9;
}

.martes {
  color: white;
  background-color: #4b9dbb;
}

.miercoles {
  color: white;
  background-color: #4b9dbb;
}

.jueves {
  color: white;
  background-color: #62a3bb;
}

.viernes {
  color: white;
  background-color: #65a1b7;
}

.sabado {
  color: white;
  background-color: #457181;
}

.domingo {
  color: white;
  background-color: #243b44;
}

.lista {
  min-height: 300px;
  width: 45%;
  margin: auto;
  background-color: aliceblue;
  padding: 30px;
  border-radius: 10px;
}
.lista h2 {
  margin: 0px;
}

.monkMode {
  min-height: 300px;
  width: 55%;
  margin: auto;
  display: flex;
  flex-direction: column;
  background-color: aliceblue;
  padding: 30px;
  border-radius: 10px;
}
.monkMode .header {
  height: 30px;
  display: flex;
  align-items: center;
  justify-items: center;
  gap: 30px;
}
.monkMode .dias {
  display: flex;
  gap: 30px;
}
.monkMode .ajustes {
  display: flex;
  gap: 30px;
}